@custom-media --wide-vp (width >= 1024px);
@custom-media --not-wide-vp (width <= 1023.9px);
@custom-media --middle-vp (768px <= width < 1023.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/montserrat-regular.woff2") format("woff2"),
    url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/montserrat-medium.woff2") format("woff2"),
    url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src:
    url("../fonts/montserrat-semibold.woff2") format("woff2"),
    url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src:
    url("../fonts/montserrat-bold.woff2") format("woff2"),
    url("../fonts/montserrat-bold.woff") format("woff");
}
:root {
  --regular-font: "Montserrat", "Impact", sans-serif;
}
:root {
  --color-base-light: #f9fbfd;
  --color-base-dark: #011c40;
  --color-bg-image: #00316a;
  --color-bg-advantages: #d9e6f5;
  --color-bg-reservation: #bbdffb;
  --color-gradient-advantages: rgba(227, 236, 246, 0.9);
  --color-bg-location: #f1efe9;
  --color-error: #ff8282;
  --color-shadow-screen: rgba(0, 0, 0, 0.25);
  --color-shadow-form: rgba(1, 28, 64, 0.15);
  --color-scroll: rgba(0, 0, 0, 0);
}
img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}
.page {
  height: 100%;
  font-family: var(--regular-font);
  color: var(--color-base-dark);
  scroll-behavior: smooth;
}
.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-base-light);

  &--noscroll {
    position: fixed;
    height: 100vh;
    overflow: hidden;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }
}
.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__wrapper-center {
  @media (--wide-vp) {
    box-sizing: border-box;
    max-width: 1250px;
    padding: 0 45px;
    margin: 0 auto;
  }

  @media (--middle-vp) {
    margin: 0 45px;
  }

  @media (--small-vp) {
    margin: 0 15px;
  }
}
.page-main {
  flex-grow: 1;
}
.page-main h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  margin-top: 0;

  @media (--middle-vp) {
    font-size: 26px;
  }

  @media (--small-vp) {
    font-size: 24px;
  }
}
.page-main h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  margin-top: 0;
}
.page-header {
  position: relative;
  background-image: url("../img/index-top-l@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  background-color: var(--color-bg-image);

  @media (--x2) {
    background-image: url("../img/index-top-l@2x.jpg");
  }

  @media (--wide-vp) {
    box-shadow: 0 4px 4px 0 var(--color-shadow-screen);
    padding-bottom: 270px;
  }

  @media (--middle-vp) {
    padding-bottom: 263px;
    background-image: url("../img/index-top-m@1x.jpg");

    @media (--x2) {
      background-image: url("../img/index-top-m@2x.jpg");
    }
  }

  @media (--small-vp) {
    padding-bottom: 165px;
    background-image: url("../img/index-top-s@1x.jpg");

    @media (--x2) {
      background-image: url("../img/index-top-s@2x.jpg");
    }
  }
}
.page-header__logo-link {
  @media (--not-small-vp) {
    margin-right: 60px;
    width: fit-content;
    max-width: 20%;
  }
}
.page-header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 20px;

  @media (--small-vp) {
    position: relative;
    justify-content: flex-start;
    padding: 10px 0;

    &--opened {
      min-height: 42px;
      margin-left: 20px;
    }

    &--opened .page-header__menu-list {
      display: block;
      position: absolute;
      z-index: 2;
      top: 75px;
      left: 0;
      right: 0;
      width: calc(100% - 20px);
      height: 100vh;
      overflow: auto;
      box-shadow: 0 -75px 0 0 var(--color-base-light), 35px -75px 0 0 var(--color-base-light);
    }

    &--opened .page-header__menu-toggle {
      margin-right: -7px;

      &:focus {
        outline: 2px solid var(--color-base-dark);
      }

      &:focus:not(:focus-visible) {
        outline: none;
      }

      &:focus-visible {
        outline: 2px solid var(--color-base-dark);
      }
    }

    &--opened .page-header__menu-toggle::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      top: 5px;
      display: block;
      mask-repeat: no-repeat;
      mask-position: center;
      background: currentColor;
      width: 24px;
      height: 24px;
      mask-image: url("../icons/sprite.svg#toggle-closed");
      margin-right: 7px;
    }

    &--opened .page-header__menu-toggle::after {
      display: none;
    }

    &--nojs .page-header__menu-list {
      display: block;
      padding-top: 75px;
    }

    &--opened .page-header__logo-link,
    &--nojs .page-header__logo-link {
      position: absolute;
      top: 10px;
      left: 35px;
      z-index: 3;

      &:focus {
        outline: 2px solid var(--color-base-dark);
        outline-offset: 2px;
      }

      &:focus:not(:focus-visible) {
        outline: none;
        outline-offset: revert;
      }

      &:focus-visible {
        outline: 2px solid var(--color-base-dark);
        outline-offset: 2px;
      }
    }

    &--opened .page-header__logo-link {
      left: 15px;
    }

    &--opened .page-header__logo-link svg,
    &--nojs .page-header__logo-link svg {
      fill: var(--color-base-dark);
    }
  }
}
.page-header__menu-nav {
  @media (--small-vp) {
    margin-top: -10px;
    margin-left: 20px;
    width: 100%;
  }
}
.page-header__menu-list {
  @media (--middle-vp) {
    font-size: 14px;
    line-height: 17px;
  }

  @media (--small-vp) {
    font-size: 18px;
    line-height: 22px;
    width: 100%;
    background-color: var(--color-base-light);
    box-shadow: 15px 0 0 0 var(--color-base-light);
    display: none;
  }
}
.page-header__menu-toggle {
  position: absolute;
  top: 15px;
  right: 0;
  margin-right: -2px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  text-align: center;
  z-index: 3;

  &:focus {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }

  @media (--small-vp) {
    display: block;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 5px;
    margin: auto;
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: currentColor;
    color: var(--color-base-light);
    width: 24px;
    height: 24px;
    mask-image: url("../icons/sprite.svg#toggle-open");
  }
}
.page-header__menu-list li {
  @media (--not-small-vp) {
    padding-top: 5px;
    margin-bottom: 5px;
  }

  @media (--small-vp) {
    margin-left: 15px;

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
}
.page-header__menu-list a {
  @media (--small-vp) {
    color: var(--color-base-dark);
    padding: 10px;

    &:focus {
      outline: 2px solid var(--color-base-dark);
      outline-offset: -2px;
    }

    &:focus:not(:focus-visible) {
      outline: none;
      outline-offset: revert;
    }

    &:focus-visible {
      outline: 2px solid var(--color-base-dark);
      outline-offset: -2px;
    }

    &:hover {
      opacity: 80%;
    }

    &:active {
      opacity: 60%;
    }
  }
}
.page__footer-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  padding-top: 120px;
  padding-bottom: 120px;

  @media (--middle-vp) {
    gap: 30px;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (--small-vp) {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    padding-top: 59px;
    padding-bottom: 61px;
  }
}
.page-footer__logo-link {
  @media (--small-vp) {
    margin-bottom: 13px;
  }
}
.page-footer__location {
  background-color: var(--color-bg-location);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  min-height: 306px;

  @media (--middle-vp) {
    min-height: 335px;
  }

  @media (--small-vp) {
    grid-row: -2 / -1;
    min-height: 316px;
  }
}
.page-footer__iframe {
  border: none;
  grid-row: 1 / -1;
  grid-column: 1 /-1;
  width: 100%;
  height: 306px;

  @media (--middle-vp) {
    height: 335px;
  }

  @media (--small-vp) {
    height: 316px;
  }
}
.page-footer__noscript {
  grid-row: 1 / -1;
  grid-column: 1 /-1;
}
.page-footer__contacts {
  margin-left: 60px;

  @media (--not-wide-vp) {
    margin-left: 0;
  }

  @media (--small-vp) {
    grid-row: 1 / 2;
  }
}
.page-footer__noscript img {
  display: block;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 306px;

  @media (--middle-vp) {
    height: 335px;
  }

  @media (--small-vp) {
    height: 316px;
  }
}
.page-footer__container {
  background-color: var(--color-base-dark);
  padding: 35px 0 33px;

  @media (--middle-vp) {
    padding: 31px 0;
  }

  @media (--small-vp) {
    padding: 30px 0 19px;
  }
}
.page-footer__wrapper {
  display: flex;
  align-items: center;

  @media (--small-vp) {
    flex-direction: column;
    align-items: flex-start;
  }
}
.page-footer__main-nav {
  margin-right: auto;
  margin-left: 40px;

  @media (--not-wide-vp) {
    font-size: 12px;
    line-height: 15px;
  }

  @media (--middle-vp) {
    margin-left: 25px;
  }

  @media (--small-vp) {
    margin: 0;
  }
}
.page-footer__social {
  margin-right: 0;
}
.main-nav {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
.main-nav__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;

  &--footer li {
    padding-top: 5px;
    margin-bottom: 5px;
  }

  @media (--not-wide-vp) {
    &--footer {
      font-size: 12px;
      line-height: 15px;
    }
  }

  @media (--not-small-vp) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (--small-vp) {
    &--footer li:not(:last-child) {
      margin-bottom: 12px;
    }
  }
}
.main-nav__item {
  &:not(:last-child) {
    @media (--not-small-vp) {
      margin-right: 33px;
    }

    @media (--middle-vp) {
      margin-right: 24px;
    }
  }
}
.main-nav__link {
  display: block;
  color: var(--color-base-light);
  text-decoration: none;
  width: fit-content;
  user-select: none;

  &:focus {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}
.presentation p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;

  @media (--wide-vp) {
    margin-right: 30px;
  }
}
.presentation {
  padding-top: 115px;
  padding-bottom: 120px;

  @media (--middle-vp) {
    padding-top: 78px;
    padding-bottom: 77px;
  }

  @media (--small-vp) {
    padding-top: 57px;
    padding-bottom: 65px;
  }
}
.presentation__wrapper-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  place-self: center;
  align-items: center;


  @media (--middle-vp) {
    column-gap: 30px;
  }

  @media (--small-vp) {
    grid-template-columns: 1fr;
    row-gap: 19px;
  }
}
.presentation__wrapper-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.presentation__wrapper-content h2 {
  margin-top: 15px;
  margin-bottom: 25px;
  line-height: 1.4;


  @media (--middle-vp) {
    margin-top: 14px;
    margin-bottom: 15px;
    width: 90%;
  }

  @media (--small-vp) {
    margin-top: 0;
    margin-bottom: 15px;
    width: 90%;
  }
}
.presentation__wrapper-content p {
  margin-top: 0;
  margin-bottom: 15px;
}
.presentation__image-set {
  width: 100%;
  max-width: 560px;
  height: 301px;
  background-color: var(--color-bg-image);

  @media (--middle-vp) {
    width: 324px;
    height: 264px;
    place-self: center;
  }

  @media (--small-vp) {
    width: 290px;
    height: 186px;
    place-self: center;
  }
}
.advantages {
  position: relative;
  padding-top: 110px;
  padding-bottom: 120px;
  background:
    linear-gradient(
      var(--color-gradient-advantages),
      var(--color-gradient-advantages)
    ),
    url("../img/advantages-bg-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  background-color: var(--color-bg-advantages);

  @media (--x2) {
    background:
      linear-gradient(
        var(--color-gradient-advantages),
        var(--color-gradient-advantages)
      ),
      url("../img/advantages-bg-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }

  @media (--middle-vp) {
    padding-top: 90px;
    padding-bottom: 100px;
    background:
      linear-gradient(
        var(--color-gradient-advantages),
        var(--color-gradient-advantages)
      ),
      url("../img/advantages-bg-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;

    @media (--x2) {
      background:
        linear-gradient(
          var(--color-gradient-advantages),
          var(--color-gradient-advantages)
        ),
        url("../img/advantages-bg-m@2x.jpg") no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  @media (--small-vp) {
    padding-top: 57px;
    padding-bottom: 64px;
    background:
      linear-gradient(
        var(--color-gradient-advantages),
        var(--color-gradient-advantages)
        ),
      url("../img/advantages-bg-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;

    @media (--x2) {
      background:
        linear-gradient(
          var(--color-gradient-advantages),
          var(--color-gradient-advantages)
          ),
        url("../img/advantages-bg-s@2x.jpg") no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
}
.advantages h2 {
  text-align: center;
  margin-bottom: 49px;

  @media (--small-vp) {
    margin-bottom: 30px;
  }
}
.advantages h3 {
  padding-top: 9px;
  margin-bottom: 10px;

  @media (--middle-vp) {
    padding-top: 7px;
  }

  @media (--small-vp) {
    padding-top: 7px;
    margin-bottom: 9px;
    width: 75%;
  }
}
.advantages p {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  margin-top: 0;
  margin-bottom: 5px;

  @media (--wide-vp) {
    margin-right: 10px;
  }

  @media (--small-vp) {
    margin-right: 7px;
  }
}
.advantages__list {
  list-style-type: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  margin: 0;

  @media (--not-wide-vp) {
    gap: 30px;
  }

  @media (--small-vp) {
    grid-template-columns: 1fr;
  }
}
.advantages__item {
  background-color: var(--color-base-light);
  padding: 72px 30px 20px;
  background-repeat: no-repeat;
  background-size: 42px 42px;
  background-position: left 30px top 30px;

  @media (--not-wide-vp) {
    padding: 62px 15px 15px 20px;
    background-size: 37px 37px;
    background-position: left 20px top 25px;

    &--boat br {
      display: none;
    }
  }

  @media (--middle-vp) {
    &--included br {
      display: none;
    }
  }

  &--ship {
    background-image: url("../icons/sprite.svg#advantages-ship");
  }

  &--included {
    background-image: url("../icons/sprite.svg#advantages-included");
  }

  &--boat {
    background-image: url("../icons/sprite.svg#advantages-boat");
  }
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  list-style-type: none;

  @media (--not-small-vp) {
    max-width: 150px;
    margin-left: 50px;
  }
}
.social__item {
  padding-top: 5px;
  margin-bottom: 5px;

  &:not(:last-child) {
    margin-right: 27px;

    @media (--not-wide-vp) {
      margin-right: 23px;
    }
  }
}
.social__link {
  display: block;
  color: var(--color-base-light);

  &:focus {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }

  &::before {
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: currentColor;
    width: 20px;
    height: 20px;

    @media (--not-wide-vp) {
      width: 16px;
      height: 16px;
    }
  }

  &--facebook::before {
    mask-image: url("../icons/sprite.svg#social-facebook");
    margin-left: -8px;

    @media (--small-vp) {
      margin-left: -7px;
    }
  }

  &--instagram::before {
    mask-image: url("../icons/sprite.svg#social-instagram");
  }

  &--twitter::before {
    mask-image: url("../icons/sprite.svg#social-twitter");
  }
}
.cruises {
  padding-top: 110px;
  padding-bottom: 120px;

  @media (--middle-vp) {
    padding-top: 90px;
    padding-bottom: 100px;
  }

  @media (--small-vp) {
    padding-top: 57px;
    padding-bottom: 65px;
  }
}
.cruises h2 {
  text-align: center;
}
.cruises__title h3 {
  margin: 0;
}
.cruises__list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 30px;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 0;
  margin-top: 49px;

  @media (--middle-vp) {
    grid-template-columns: repeat(auto-fill, minmax(309px, 1fr));
  }

  @media (--small-vp) {
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 30px;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  }
}
.cruises__item {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  color: var(--color-base-light);
  background-color: var(--color-base-dark);
  width: 260px;
  height: 386px;
  --card-visible: -1;
  place-self: center;

  @media (--middle-vp) {
    width: 309px;
  }

  @media (--small-vp) {
    width: 270px;
  }
}
.cruises__title,
.cruises__card,
.cruises__image-set {
  box-sizing: border-box;
  grid-column: 1 /-1;
}
.cruises__title {
  margin-top: auto;
  margin-bottom: 22px;
  margin-left: 30px;
  grid-row: 1 / -1;

  @media (--middle-vp) {
    margin-bottom: 27px;
    margin-left: 35px;
  }

  @media (--small-vp) {
    margin-bottom: 16px;
    margin-left: 25px;
  }
}
.cruises__card {
  display: flex;
  flex-direction: column;
  padding: 0 25px;
  z-index: var(--card-visible);
  margin-bottom: 31px;
  grid-row: 2 / -1;
  overflow-y: auto;

  @media (--wide-vp) {
    scrollbar-color: var(--color-base-dark) var(--color-base-light);
    scrollbar-width: thin;

    &::-webkit-scrollbar {
      width: 12px;
      background-color: var(--color-base-light);
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--color-base-dark);
      box-shadow: inset 1px 1px 2px var(--color-base-light);
      border-radius: 4px;

      &:hover {
        box-shadow: inset 1px 1px 5px var(--color-base-light);
      }
    }

    &::-webkit-scrollbar-button:vertical:start:decrement {
      background:
        linear-gradient(120deg, var(--color-base-dark) 40%, var(--color-scroll) 41%),
        linear-gradient(240deg, var(--color-base-dark) 40%, var(--color-scroll) 41%),
        linear-gradient(0deg, var(--color-base-dark) 30%, var(--color-scroll) 31%);
      background-color: var(--color-base-light);

      &:hover {
        box-shadow: inset 1px 1px 2px var(--color-base-light);
      }

      &:active {
        box-shadow: inset 1px 1px 4px var(--color-base-light);
      }
    }

    &::-webkit-scrollbar-button:vertical:end:increment {
      background:
        linear-gradient(300deg, var(--color-base-dark) 40%, var(--color-scroll) 41%),
        linear-gradient(60deg, var(--color-base-dark) 40%, var(--color-scroll) 41%),
        linear-gradient(180deg, var(--color-base-dark) 30%, var(--color-scroll) 31%);
      background-color: var(--color-base-light);

      &:hover {
        box-shadow: inset 1px 1px 2px var(--color-base-light);
      }

      &:active {
        box-shadow: inset 1px 1px 4px var(--color-base-light);
      }
    }
  }

  @media (--middle-vp) {
    padding: 0 35px;
  }
}
.cruises__image-set {
  grid-row: 1 / -1;
}
.cruises__specification {
  display: flex;
  flex-direction: column;
  margin: 0;
}
.cruises__specification-set {
  margin-bottom: 8px;
}
.cruises__specification-set dt {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.4;
  margin-bottom: 3px;
}
.cruises__specification-set dd {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  margin-left: 0;
  margin-bottom: 2px;
}
.cruises__image-set img {
  width: 100%;
  height: 386px;
  margin-bottom: 0;
}
.cruises__button {
  box-sizing: border-box;
  display: grid;
  width: 100%;
  height: fit-content;
  place-items: center;
  margin-top: auto;
  margin-bottom: 4px;
  padding: 12px;
  padding-bottom: 7px;

  &:focus {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}
.cruises__item:focus-within .cruises__image-set,
.cruises__item:hover .cruises__image-set {
  z-index: -1;
}
.cruises__item:focus-within .cruises__card,
.cruises__item:hover .cruises__card {
  --card-visible: 2;
}
.cruises__item:focus-within .cruises__title,
.cruises__item:hover .cruises__title {
