.screen {
  position: relative;
  color: var(--color-base-light);
  display: flex;
  flex-direction: column;


  @media (--wide-vp) {
    padding: 161px 95px 0;
  }

  @media (--middle-vp) {
    padding-top: 190px;
  }

  @media (--small-vp) {
    padding-top: 115px;
  }
}
.form__fieldset {
  border: none;
}
.form__list,
.form__fieldset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.form__item {
  margin-bottom: 20px;
}
.form input,
.form__dispatch-button {
  &:focus {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--color-base-light);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
    cursor: pointer;
  }

  &:active {
    opacity: 60%;
  }

  &:disabled {
    cursor: not-allowed;
  }

  &--error[required] {
    outline: 2px solid var(--color-error);
    outline-offset: -2px;
  }
}
.form__item input {
  box-sizing: border-box;
  font-family: var(--regular-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  font-style: normal;
  color: var(--color-base-dark);
  border: none;
  border-radius: 0;
  width: 100%;
  height: 43px;
  padding: 0 16px;

  &:not(:placeholder-shown) {
    outline: none;
  }
}
.form__checkbox-item {
  margin-bottom: 30px;

  @media (--small-vp) {
    margin-top: -3px;
    margin-bottom: 27px;
  }
}
.form__checkbox-item input {
  display: grid;
  position: relative;
  margin: 0;
  appearance: none;
  place-items: center;

  &::before {
    display: block;
    box-sizing: border-box;
    content: "";
    width: 18px;
    height: 18px;
    background-color: var(--color-base-light);
    border: none;

    @media (--small-vp) {
      width: 20px;
      height: 20px;
    }
  }

  &:checked::after {
    position: absolute;
    display: block;
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../icons/sprite.svg#checkbox-request");
    width: 18px;
    height: 18px;
  }
}
.form__checkbox-label {
  display: flex;
  align-items: center;
}
.form__checkbox-control {
  color: var(--color-base-light);
  font-weight: 400;
  font-size: 10px;
  line-height: 1.3;
  margin-left: 5px;
  text-align: left;

  @media (--small-vp) {
    margin-left: 8px;
    width: 65%;
  }
}
.form__dispatch-button {
  width: 100%;
  min-height: 43px;
  padding: 5px 20px;
}
.contacts {
